.approach {
  &__top {
    padding: 80px;
    background-color: #F7F8F8;
  }
  &__left {
    grid-column: 5 span;
    padding-top: 33px;
  }
  &__right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    grid-column: 7 / 6  span;
    gap: 8px;
  }
  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: var(--dark-color);
  }
  &__descr {
    margin: 0;
    margin-bottom: 37px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--text-color);

  }
  &__btn {
    border-color: rgba(53, 83, 98, 0.2);
    padding: 16px 34px;
    color: #00413e;

  }

  &__bottom {
    padding: 24px 0;
    background-color: var(--light-color);
  }
  &__item {
    grid-column: 2 span;
  }
  &__link {
    display: block;
    border-top: 1px solid rgba(53, 83, 98, 0.8);
    padding-top: 16px;
    padding-left: 32px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--text-color);
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
    background-position: left top 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;


    &--ted {
      background-image: url(../img/approach-icon-1.png);
    }
    &--nyt {
      background-image: url(../img/approach-icon-2.png);
    }
    &--scoop {
      background-image: url(../img/approach-icon-3.png);
    }
    &--press {
      background-image: url(../img/approach-icon-4.png);
    }
    &--time {
      background-image: url(../img/approach-icon-5.png);
    }
    &--freelink {
      background-image: url(../img/approach-icon-6.png);
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
    }
    &:active {
      border-color: var(--active-color);
      color: var(--active-color);
    }

  }
}
.approach__link.focus-visible {
  border-color: var(--active-color);
}
