.footer {
  &__top {
    padding-top: 112px;
    padding-bottom: 137px;
    background-color: #001414;
  }
  &__bottom {

    background-color: #000F0F;

    &-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &-text {
      display: flex;
      align-items: center;

    }

  }
  &__text {
    padding-top: 45px;
    padding-bottom: 63px;
  }

  &__column {
    padding-top: 40px;
    padding-bottom: 63px;
    grid-column: 2 span;

    &:last-child {
      grid-column: 11 / 2 span ;
    }
  }
  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
    color: #EDECEB;
  }
  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 23px;
    padding-bottom: 24px;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #7D9798;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background-color: #25B895;
    }

  }
  &__sublist {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    gap: 8px;

    &-link {
      font-weight: 400;
      font-size: 9px;
      line-height: 10px;
      color: #E5ECEC;

    }
  }
  &__copy {
    font-weight: 400;
    font-size: 9px;
    line-height: 10px;
    color: #E5ECEC;
  }
  &__rights {
    font-weight: 400;
    font-size: 10px;
    line-height: 10px;
    color: #E5ECEC;
  }
}
